<template>
  <section class="hero is-fullheight">
    <div class="hero-body">
      <div class="container">
        <div v-if="isbday === false">
          <h1 class="bday-text">{{now}}...</h1>
        </div>
        <div v-if="isbday === true">
          <figure class="image">
            <img :src="selectedImg" width="100%">
          </figure>
          <br>
          <br>
          <div>
            <div class="cake">
              <div class="icing">
                <div class="line"></div>
                <div class="squiggle"></div>
                <div class="squiggle2"></div>
                <div class="squiggle3"></div>
              </div>
              <div class="candle"></div>
              <div class="flame"></div>
            </div>
            <div class="bday-text happybday">Happy birthday!</div>
            <div class="bday-text person">Chonnikan Fakhaw</div>
          </div>
        </div>
      </div>
    </div>
  </section>
</template>

<style>
.person {
  font-size: 65px;
  color: #450a2a;
}

.cake {
  position: relative;
  margin: 0 auto;
  width: 35px;
  height: 18px;
  background-color: #f2d4cc;
  border-radius: 0px 0px 3px 3px;
}

.icing {
  position: absolute;
  top: -10px;
  width: 0;
  height: 0;
  border-left: 30px solid transparent;
  border-right: 5px solid transparent;
  border-bottom: 10px solid #eaa19d;
}

.squiggle {
  position: absolute;
  top: 8px;
  width: 5px;
  height: 5px;
  border-radius: 50%;
  background-color: #eaa19d;
}
.squiggle:before {
  content: "";
  position: absolute;
  top: 0px;
  left: -5px;
  width: 5px;
  height: 5px;
  background-color: #eaa19d;
  border-radius: 50%;
}
.squiggle:after {
  content: "";
  position: absolute;
  top: 0px;
  left: -10px;
  width: 5px;
  height: 5px;
  background-color: #eaa19d;
  border-radius: 50%;
}

.squiggle2 {
  position: absolute;
  top: 8px;
  left: -15px;
  width: 5px;
  height: 5px;
  background-color: #eaa19d;
  border-radius: 50%;
}
.squiggle2:before {
  content: "";
  position: absolute;
  top: 0px;
  left: -5px;
  width: 5px;
  height: 5px;
  background-color: #eaa19d;
  border-radius: 50%;
}
.squiggle2:after {
  content: "";
  position: absolute;
  top: 0px;
  left: -10px;
  width: 5px;
  height: 5px;
  background-color: #eaa19d;
  border-radius: 50%;
}

.squiggle3 {
  position: absolute;
  top: 10px;
  left: -30px;
  width: 5px;
  height: 2.5px;
  background-color: #eaa19d;
  border-radius: 0px 0px 2.5px 2.5px;
}

.line {
  position: absolute;
  top: 20px;
  left: -30px;
  width: 35px;
  height: 2px;
  background-color: #eaa19d;
}

.candle {
  position: absolute;
  top: -12px;
  left: 18px;
  width: 3px;
  height: 10px;
  background-color: white;
}

.flame {
  position: absolute;
  border-radius: 50%;
  top: -15px;
  left: 18px;
  width: 3px;
  height: 3px;
  background-color: #ffbe22;
}
</style>

<script>
import moment from "moment";
import gade1 from "../assets/gade1.jpg";
import gade2 from "../assets/gade2.jpg";
import gade3 from "../assets/gade3.jpg";
import gade4 from "../assets/gade4.jpg";
import gade5 from "../assets/gade5.jpg";
import gade6 from "../assets/gade6.jpg";
import gade7 from "../assets/gade7.jpg";
import gade8 from "../assets/gade8.jpg";
import gade9 from "../assets/gade9.jpg";
import gade10 from "../assets/gade10.jpg";
import gade11 from "../assets/gade11.jpg";
import gade12 from "../assets/gade12.jpg";
import gade13 from "../assets/gade13.jpg";
import gade14 from "../assets/gade14.jpg";

export default {
  data() {
    return {
      imgs: [
        gade1,
        gade2,
        gade3,
        gade4,
        gade5,
        gade6,
        gade7,
        gade8,
        gade9,
        gade10,
        gade11,
        gade12,
        gade13,
        gade14
      ],
      selectedImg: null,
      isbday: Boolean,
      now: null
    };
  },
  created() {
    this.updateTime();
    this.checkIsBday();
    this.randomPic();
    setInterval(() => {
      this.checkIsBday();
      this.updateTime();
    }, 1000);
    setInterval(() => {
      this.randomPic();
    }, 2000);
  },
  methods: {
    randomPic: function() {
      let idx = Math.floor(Math.random() * this.imgs.length);
      this.selectedImg = this.imgs[idx];
    },
    updateTime: function() {
      this.now = moment()
        .endOf("day")
        .fromNow();
    },
    checkIsBday: function() {
      this.isbday = moment().isSameOrAfter(moment("2019-03-21", "YYYY-MM-DD"));
    }
  }
};
</script>


