<template>
  <nav class="navbar is-fixed-top is-dark">
    <div class="navbar-brand">
      <a class="navbar-item">
        <figure class="image is-24x24 has-image-centered">
          <img class="is-rounded avatar" src="../assets/noface.jpg" alt="logo">
        </figure>
      </a>
    </div>
    <div class="navbar-start">
      <router-link
        v-for="routes in links"
        v-bind:key="routes.id"
        :to="`${routes.page}`"
        class="navbar-item"
      >{{routes.text}}</router-link>
    </div>
  </nav>
</template>

<script>
export default {
  name: "Navbar",
  data() {
    return {
      links: [
        { id: 0, text: "About", page: "/" },
        { id: 1, text: "Hbd", page: "/hbd" }
      ]
    };
  }
};
</script>



